import { useState } from "react";
import "./styles.css";

export default function App() {
   const time = new Date().toLocaleTimeString();
const[ttime,settime]= useState(time);
const Chng = () => {
const rtime = new Date().toLocaleTimeString();
settime(rtime);
};
setInterval(Chng,1000)
  return (
    <div className="App">
      <span className= "btn"> Digital Clock</span>
      <h1 className ="header">{ttime}</h1>
      {/* <button className="btn" onClick={Chng}>Click Me</button> */}
    </div>
  );
}
